


img { max-width: 100%; }

figure { margin: 0; }




.rwd-test {
  &__div {
    width: 100%;
    height: 200px;
    background-color: pink;
    color: #ffffff;
    margin-bottom: 45px;
    padding: 45px;
    transition: background-color 0.2s ease;
    @include mq($bp-small, min) { background-color: green; }
    @include mq($bp-medium, min) { background-color: blue; }
    @include mq($bp-large, min ) { background-color: red; }
    @include mq($bp-xlarge, min) { background-color: black; }
  }
}

.l-content {
  @extend %l-content-pad;
  margin: 0 auto;
  max-width: 1400px;
  width: 100%;
}



.test {
  &__group {
    @include clearfix;
    // padding: 45px;
    // @extend %l-content-pad;
    padding-left: $spacer-medium;
    padding-right: $spacer-medium;
    padding-top: $spacer-xlarge;
    padding-bottom: $spacer-xlarge;
    background-color: #fff;
    border: 1px solid #ccc;
    @include mq(40rem) {
      padding-left: $spacer-xlarge;
      padding-right: $spacer-xlarge;
      // max-width: 960px;
      max-width: 1200px;
    }

    & + & {
      position: relative;
      margin-top: $spacer-xxlarge;
      // &:before {
      //   position: absolute;
      //   content: '';
      //   top: -45px;
      //   left: 0;
      //   right: 0;
      //   height: 1px;
      //   background-color: #ccc;
      // }
    }
    &--bg { background-color: #fff; }
    &--dark { background-color: #232a31; }
  } //__group


  &__sub-section {
    & + & { margin-top: $spacer-xxlarge;}
  }


  &__h {
    font-family: $font-display;
    font-size: $display-size-h3;
    font-weight: $font-weight-bold;
    margin-bottom: $spacer-small;
    // font-weight: $font-weight-reg;
    &--sub {
      font-family: $font-body;
      font-size: $body-size-xsmall;
      // font-size: 0.7rem;
      margin-top: 2rem;
      text-transform: uppercase;
      margin-bottom: 1rem;
      color: #666;
      letter-spacing: 2px;
    }

  } // __h


  &__box {
    margin-bottom: 24px;
    background-color: $ui-gray-light;
    padding: 1rem;
  } // box

  &__box--white {
    margin-bottom: 45px;
    background-color: #fff;
    padding: 0;
    clear: left;
  }


  &__notify {
    background-color: $ui-gray-light;
    border-left: 2px solid $ui-orange;
    padding: 0.15rem 1rem;
  }

  &__notify--alert {
    background-color: $ui-orange;
    // border-left: 2px solid darken($ui-orange, 10%);
    padding: 0.15rem 1rem;
    color: #fff;
  }


  &__divider {
    width: 100%;
    height: 1px;
    background-color: #ccc;
    margin: $spacer-xlarge 0;
  } // divider

  &__color-section {
    & + & { margin-top: 24px; }
  }

  &__color-item {
    width: 150px;
    height: 150px;
    background-color: pink;
    float: left;
  }


} // test



