// Testing Fonts
// --------------------------------------------

.type__h {
  font-size: 2rem;
  font-family: $font-display;
  & + & { margin-top: 1rem; }
  & + .test__h--sub { margin-top: 75px; }
}



.type {
  &--reg {
    &--ital {
      font-style: italic;
    }
  }

  &--bold {
    font-weight: $font-weight-bold;
    &--ital {
      font-weight: $font-weight-bold;
      font-style: italic;
    }
  }

  &--cond {
    &--reg {
      font-family: $font-display-cond;
      &--ital {
        font-family: $font-display-cond;
        font-style: italic;
      }
    }
  }

  &--cond--bold {
    font-family: $font-display-cond;
    font-weight: $font-weight-bold;
    &--ital {
      font-family: $font-display-cond;
      font-weight: $font-weight-bold;
      font-style: italic;
    }
  }

  &--heavy {
    font-weight: $font-weight-heavy;
    &--cond {
      font-family: $font-display-cond;
      font-weight: $font-weight-heavy;
    }
  }
}




.feed {
  &__meta {
    border: 1px solid pink;
    margin-bottom: type-scale(0);
  }

  &__item {
    max-width: 496px;
    margin: 0 auto;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
    padding: type-scale(0);
    border-radius: 2px;
  }
}




